"use client"

import { encryptAES256, decryptAES256 } from '@/lib/security'

// AI Service Types
export type AIServiceType = 'text' | 'image' | 'audio' | 'video' | 'music'

export interface AIService {
  id: string
  name: string
  type: AIServiceType
  provider: 'openai' | 'replicate' | 'huggingface' | 'local' | 'custom'
  endpoint?: string
  isActive: boolean
  isLocal: boolean
  model?: string
  description: string
}

export interface AIRequest {
  service: string
  prompt: string
  options?: Record<string, any>
  userId: string
}

export interface AIResponse {
  success: boolean
  data?: any
  error?: string
  usage?: {
    tokens?: number
    cost?: number
    time?: number
  }
}

