// AI Service Manager Class
export class AIServiceManager {
  private apiKeys: Map<string, string> = new Map()
  private serviceStatus: Map<string, boolean> = new Map()

  constructor() {
    // Initialize service status
    Object.values(AI_SERVICES).forEach(service => {
      this.serviceStatus.set(service.id, service.isActive)
    })
  }

  // Set API key for a service (encrypted storage)
  setApiKey(serviceId: string, apiKey: string): void {
    const encryptedKey = encryptAES256(apiKey)
    this.apiKeys.set(serviceId, encryptedKey)
    
    // Store in localStorage for persistence (in production, use secure backend storage)
    if (typeof window !== 'undefined') {
      localStorage.setItem(`ai_key_${serviceId}`, encryptedKey)
    }
  }

  // Get API key for a service (decrypted)
  getApiKey(serviceId: string): string | null {
    let encryptedKey = this.apiKeys.get(serviceId)
    
    // Try to load from localStorage if not in memory
    if (!encryptedKey && typeof window !== 'undefined') {
      const storedKey = localStorage.getItem(`ai_key_${serviceId}`)
      if (storedKey) {
        encryptedKey = storedKey
        this.apiKeys.set(serviceId, encryptedKey)
      }
    }
    
    if (!encryptedKey) return null
    
    try {
      return decryptAES256(encryptedKey)
    } catch (error) {
      console.error('Failed to decrypt API key:', error)
      return null
    }
  }

  // Toggle service active status
  toggleService(serviceId: string, isActive: boolean): void {
    this.serviceStatus.set(serviceId, isActive)
    
    // Store in localStorage
    if (typeof window !== 'undefined') {
      localStorage.setItem(`ai_status_${serviceId}`, isActive.toString())
    }
  }

  // Check if service is active and available
  isServiceAvailable(serviceId: string): boolean {
    const service = AI_SERVICES[serviceId]
    if (!service) return false
    
    const isActive = this.serviceStatus.get(serviceId) ?? service.isActive
    
    // Local services are always available if active
    if (service.isLocal) return isActive
    
    // External services need API keys
    return isActive && this.getApiKey(serviceId) !== null
  }

  // Get available services by type
  getAvailableServices(type?: AIServiceType): AIService[] {
    return Object.values(AI_SERVICES)
      .filter(service => !type || service.type === type)
      .filter(service => this.isServiceAvailable(service.id))
  }

  // Get preferred service for a type (local first, then external)
  getPreferredService(type: AIServiceType): AIService | null {
    const availableServices = this.getAvailableServices(type)
    
    // Prefer local services
    const localService = availableServices.find(s => s.isLocal)
    if (localService) return localService
    
    // Fall back to external services
    return availableServices[0] || null
  }

  // Make AI request
  async makeRequest(request: AIRequest): Promise<AIResponse> {
    const service = AI_SERVICES[request.service]
    if (!service) {
      return { success: false, error: 'Service not found' }
    }

    if (!this.isServiceAvailable(service.id)) {
      return { success: false, error: 'Service not available' }
    }

    try {
      if (service.isLocal) {
        return await this.makeLocalRequest(service, request)
      } else {
        return await this.makeExternalRequest(service, request)
      }
    } catch (error) {
      console.error('AI request failed:', error)
      return { success: false, error: error instanceof Error ? error.message : 'Unknown error' }
    }
  }

  // Make request to local AI service
  private async makeLocalRequest(service: AIService, request: AIRequest): Promise<AIResponse> {
    // This will be implemented with actual local AI models
    // For now, return mock responses
    
    const startTime = Date.now()
    
    // Simulate processing time
    await new Promise(resolve => setTimeout(resolve, 1000 + Math.random() * 2000))
    
    const processingTime = Date.now() - startTime
    
    switch (service.type) {
      case 'text':
        return {
          success: true,
          data: {
            text: `[Local AI Response] ${request.prompt}\n\nThis is a simulated response from the local AI model. In production, this would be generated by the actual local LLM.`,
            model: service.name
          },
          usage: { time: processingTime }
        }
      
      case 'image':
        return {
          success: true,
          data: {
            url: `https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/3d6da194-178c-416e-93ba-a164c21c3e5d.png 20))}`,
            prompt: request.prompt,
            model: service.name
          },
          usage: { time: processingTime }
        }
      
      case 'music':
        return {
          success: true,
          data: {
            url: 'data:audio/wav;base64,UklGRnoGAABXQVZFZm10IBAAAAABAAEAQB8AAEAfAAABAAgAZGF0YQoGAACBhYqFbF1fdJivrJBhNjVgodDbq2EcBj+a2/LDciUFLIHO8tiJNwgZaLvt559NEAxQp+PwtmMcBjiR1/LMeSwFJHfH8N2QQAoUXrTp66hVFApGn+DyvmwhBSuBzvLZiTYIG2m98OScTgwOUarm7blmGgU7k9n1unEiBC13yO/eizEIHWq+8+OWT',
            prompt: request.prompt,
            model: service.name
          },
          usage: { time: processingTime }
        }
      
      default:
        return { success: false, error: 'Unsupported service type' }
    }
  }

  // Make request to external AI service
  private async makeExternalRequest(service: AIService, request: AIRequest): Promise<AIResponse> {
    const apiKey = this.getApiKey(service.id)
    if (!apiKey) {
      return { success: false, error: 'API key not configured' }
    }

    const startTime = Date.now()

    try {
      // This will be implemented with actual API calls
      // For now, return mock responses indicating external service would be called
      
      await new Promise(resolve => setTimeout(resolve, 500 + Math.random() * 1000))
      
      const processingTime = Date.now() - startTime
      
      return {
        success: true,
        data: {
          text: `[External AI Service] This would be a real response from ${service.name}`,
          model: service.model || service.name
        },
        usage: { time: processingTime }
      }
    } catch (error) {
      return { 
        success: false, 
        error: error instanceof Error ? error.message : 'External service error' 
      }
    }
  }
}

// Global AI service manager instance
export const aiServiceManager = new AIServiceManager()

// Helper functions for common AI operations
export async function generateText(prompt: string, userId: string, options?: Record<string, any>): Promise<AIResponse> {
  const service = aiServiceManager.getPreferredService('text')
  if (!service) {
    return { success: false, error: 'No text generation service available' }
  }

  return aiServiceManager.makeRequest({
    service: service.id,
    prompt,
    userId,
    options
  })
}

