export async function transcribeAudio(audioData: Blob, userId: string, options?: Record<string, any>): Promise<AIResponse> {
  const service = aiServiceManager.getPreferredService('audio')
  if (!service) {
    return { success: false, error: 'No audio processing service available' }
  }

  return aiServiceManager.makeRequest({
    service: service.id,
    prompt: 'transcribe',
    userId,
    options: { ...options, audioData }
  })
}
