"use client"

import { useState, useEffect } from 'react'
import { useAuth } from '@/components/auth-provider'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Switch } from '@/components/ui/switch'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Badge } from '@/components/ui/badge'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'
import { toast } from 'sonner'
import { AI_SERVICES, aiServiceManager } from '@/lib/ai-services'

interface ApiKey {
  id: string
  name: string
  service: string
  is_active: boolean
  created_by: string
  created: string
  last_used?: string
  usage_count: number
  masked_key: string
}

export function GodModePanel() {
  const { user } = useAuth()
  const [apiKeys, setApiKeys] = useState<ApiKey[]>([])
  const [loading, setLoading] = useState(true)
  const [newKeyDialog, setNewKeyDialog] = useState(false)
  const [testApiDialog, setTestApiDialog] = useState(false)

  // New API key form
  const [newKeyForm, setNewKeyForm] = useState({
    name: '',
    service: '',
    apiKey: ''
  })

  // API test form
  const [testForm, setTestForm] = useState({
    service: '',
    prompt: 'Hello, this is a test prompt',
    testing: false
  })

  useEffect(() => {
    if (user?.isGodMode) {
      loadApiKeys()
    }
  }, [user])

  const loadApiKeys = async () => {
    try {
      const response = await fetch('/api/god-mode/api-keys')
      const data = await response.json()
      
      if (data.success) {
        setApiKeys(data.apiKeys)
      } else {
        toast.error('Failed to load API keys')
      }
    } catch (error) {
      toast.error('Error loading API keys')
    } finally {
      setLoading(false)
    }
  }

  const createApiKey = async () => {
    if (!newKeyForm.name || !newKeyForm.service || !newKeyForm.apiKey) {
      toast.error('All fields are required')
      return
    }

    try {
      const response = await fetch('/api/god-mode/api-keys', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(newKeyForm)
      })

      const data = await response.json()
      
      if (data.success) {
        toast.success('API key created successfully')
        setNewKeyDialog(false)
        setNewKeyForm({ name: '', service: '', apiKey: '' })
        loadApiKeys()
        
        // Update AI service manager
        aiServiceManager.setApiKey(newKeyForm.service, newKeyForm.apiKey)
        aiServiceManager.toggleService(newKeyForm.service, true)
      } else {
        toast.error(data.error || 'Failed to create API key')
      }
    } catch (error) {
      toast.error('Error creating API key')
    }
  }

  const toggleApiKey = async (keyId: string, isActive: boolean) => {
    try {
      const response = await fetch('/api/god-mode/api-keys', {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ id: keyId, is_active: isActive })
      })

      const data = await response.json()
      
      if (data.success) {
        toast.success(`API key ${isActive ? 'activated' : 'deactivated'}`)
        loadApiKeys()
      } else {
        toast.error(data.error || 'Failed to update API key')
      }
    } catch (error) {
      toast.error('Error updating API key')
    }
  }

  const deleteApiKey = async (keyId: string) => {
    if (!confirm('Are you sure you want to delete this API key?')) return

    try {
      const response = await fetch(`/api/god-mode/api-keys?id=${keyId}`, {
        method: 'DELETE'
      })

      const data = await response.json()
      
      if (data.success) {
        toast.success('API key deleted successfully')
        loadApiKeys()
      } else {
        toast.error(data.error || 'Failed to delete API key')
      }
    } catch (error) {
      toast.error('Error deleting API key')
    }
  }

  const testApiService = async () => {
    if (!testForm.service || !testForm.prompt) {
      toast.error('Service and prompt are required')
      return
    }

    setTestForm(prev => ({ ...prev, testing: true }))

    try {
      const response = await aiServiceManager.makeRequest({
        service: testForm.service,
        prompt: testForm.prompt,
        userId: user?.id || 'test'
      })

      if (response.success) {
        toast.success('API test successful!')
        console.log('API Response:', response.data)
      } else {
        toast.error(`API test failed: ${response.error}`)
      }
    } catch (error) {
      toast.error('API test error')
    } finally {
      setTestForm(prev => ({ ...prev, testing: false }))
    }
  }

  if (!user?.isGodMode) {
    return (
      <Card className="bg-red-900/20 border-red-500/20">
        <CardHeader>
          <CardTitle className="text-red-400">Access Denied</CardTitle>
          <CardDescription>God Mode access required</CardDescription>
        </CardHeader>
      </Card>
    )
  }

  return (
    <div className="space-y-6">
      <Card className="bg-red-900/20 border-red-500/20">
        <CardHeader>
          <CardTitle className="text-red-400"> God Mode Control Panel</CardTitle>
          <CardDescription>
            Super Admin access - All actions are logged and audited
          </CardDescription>
        </CardHeader>
      </Card>

      <Tabs defaultValue="api-management" className="space-y-6">
        <TabsList className="grid w-full grid-cols-5 bg-black/50">
          <TabsTrigger value="api-management">API Management</TabsTrigger>
          <TabsTrigger value="ai-services">AI Services</TabsTrigger>
          <TabsTrigger value="user-management">Users</TabsTrigger>
          <TabsTrigger value="system-logs">System Logs</TabsTrigger>
          <TabsTrigger value="data-export">Data Export</TabsTrigger>
        </TabsList>

        <TabsContent value="api-management" className="space-y-6">
          <div className="flex justify-between items-center">
            <h2 className="text-2xl font-bold text-white">API Key Management</h2>
            <Dialog open={newKeyDialog} onOpenChange={setNewKeyDialog}>
              <DialogTrigger asChild>
                <Button variant="destructive">Add API Key</Button>
              </DialogTrigger>
              <DialogContent className="bg-gray-900 border-gray-700">
                <DialogHeader>
                  <DialogTitle className="text-white">Add New API Key</DialogTitle>
                  <DialogDescription>
                    Configure a new API key for AI services
                  </DialogDescription>
                </DialogHeader>
                <div className="space-y-4">
                  <div>
                    <Label htmlFor="keyName" className="text-white">Name</Label>
                    <Input
                      id="keyName"
                      placeholder="e.g., OpenAI Production Key"
                      value={newKeyForm.name}
                      onChange={(e) => setNewKeyForm(prev => ({ ...prev, name: e.target.value }))}
                      className="bg-gray-800 border-gray-600 text-white"
                    />
                  </div>
                  <div>
                    <Label htmlFor="keyService" className="text-white">Service</Label>
                    <Select value={newKeyForm.service} onValueChange={(value) => setNewKeyForm(prev => ({ ...prev, service: value }))}>
                      <SelectTrigger className="bg-gray-800 border-gray-600 text-white">
                        <SelectValue placeholder="Select AI service" />
                      </SelectTrigger>
                      <SelectContent className="bg-gray-800 border-gray-600">
                        {Object.values(AI_SERVICES)
                          .filter(service => !service.isLocal)
                          .map(service => (
                            <SelectItem key={service.id} value={service.id}>
                              {service.name}
                            </SelectItem>
                          ))}
                      </SelectContent>
                    </Select>
                  </div>
                  <div>
                    <Label htmlFor="apiKey" className="text-white">API Key</Label>
                    <Input
                      id="apiKey"
                      type="password"
                      placeholder="Enter API key"
                      value={newKeyForm.apiKey}
                      onChange={(e) => setNewKeyForm(prev => ({ ...prev, apiKey: e.target.value }))}
                      className="bg-gray-800 border-gray-600 text-white"
                    />
                  </div>
                  <Button onClick={createApiKey} className="w-full">
                    Create API Key
                  </Button>
                </div>
              </DialogContent>
            </Dialog>
          </div>

          <div className="grid gap-4">
            {loading ? (
              <Card className="bg-black/50 border-purple-500/20">
                <CardContent className="p-6">
                  <p className="text-gray-400">Loading API keys...</p>
                </CardContent>
              </Card>
            ) : apiKeys.length === 0 ? (
              <Card className="bg-black/50 border-purple-500/20">
                <CardContent className="p-6">
                  <p className="text-gray-400">No API keys configured</p>
                </CardContent>
              </Card>
            ) : (
              apiKeys.map((key) => (
                <Card key={key.id} className="bg-black/50 border-purple-500/20">
                  <CardHeader>
                    <div className="flex items-center justify-between">
                      <div>
                        <CardTitle className="text-white">{key.name}</CardTitle>
                        <CardDescription>
                          Service: {key.service} | Created by: {key.created_by}
                        </CardDescription>
                      </div>
                      <div className="flex items-center space-x-2">
                        <Badge variant={key.is_active ? 'default' : 'secondary'}>
                          {key.is_active ? 'Active' : 'Inactive'}
                        </Badge>
                        <Switch
                          checked={key.is_active}
                          onCheckedChange={(checked) => toggleApiKey(key.id, checked)}
                        />
                      </div>
                    </div>
                  </CardHeader>
                  <CardContent>
                    <div className="flex items-center justify-between">
                      <div className="text-sm text-gray-400">
                        <p>Key: {key.masked_key}</p>
                        <p>Usage: {key.usage_count} calls</p>
                        <p>Last used: {key.last_used ? new Date(key.last_used).toLocaleDateString() : 'Never'}</p>
                      </div>
                      <Button
                        variant="destructive"
                        size="sm"
                        onClick={() => deleteApiKey(key.id)}
                      >
                        Delete
                      </Button>
                    </div>
                  </CardContent>
                </Card>
              ))
            )}
          </div>
        </TabsContent>

        <TabsContent value="ai-services" className="space-y-6">
          <div className="flex justify-between items-center">
            <h2 className="text-2xl font-bold text-white">AI Services Status</h2>
            <Dialog open={testApiDialog} onOpenChange={setTestApiDialog}>
              <DialogTrigger asChild>
                <Button variant="outline">Test API</Button>
              </DialogTrigger>
              <DialogContent className="bg-gray-900 border-gray-700">
                <DialogHeader>
                  <DialogTitle className="text-white">Test AI Service</DialogTitle>
                  <DialogDescription>
                    Test an AI service with a sample prompt
                  </DialogDescription>
                </DialogHeader>
                <div className="space-y-4">
                  <div>
                    <Label htmlFor="testService" className="text-white">Service</Label>
                    <Select value={testForm.service} onValueChange={(value) => setTestForm(prev => ({ ...prev, service: value }))}>
                      <SelectTrigger className="bg-gray-800 border-gray-600 text-white">
                        <SelectValue placeholder="Select service to test" />
                      </SelectTrigger>
                      <SelectContent className="bg-gray-800 border-gray-600">
                        {Object.values(AI_SERVICES).map(service => (
                          <SelectItem key={service.id} value={service.id}>
                            {service.name} ({service.isLocal ? 'Local' : 'External'})
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>
                  <div>
                    <Label htmlFor="testPrompt" className="text-white">Test Prompt</Label>
                    <Input
                      id="testPrompt"
                      placeholder="Enter test prompt"
                      value={testForm.prompt}
                      onChange={(e) => setTestForm(prev => ({ ...prev, prompt: e.target.value }))}
                      className="bg-gray-800 border-gray-600 text-white"
                    />
                  </div>
                  <Button 
                    onClick={testApiService} 
                    className="w-full"
                    disabled={testForm.testing}
                  >
                    {testForm.testing ? 'Testing...' : 'Test Service'}
                  </Button>
                </div>
              </DialogContent>
            </Dialog>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {Object.values(AI_SERVICES).map((service) => {
              const isAvailable = aiServiceManager.isServiceAvailable(service.id)
              return (
                <Card key={service.id} className="bg-black/50 border-purple-500/20">
                  <CardHeader>
                    <div className="flex items-center justify-between">
                      <CardTitle className="text-white text-sm">{service.name}</CardTitle>
                      <Badge variant={isAvailable ? 'default' : service.isLocal ? 'secondary' : 'destructive'}>
                        {isAvailable ? 'Available' : service.isLocal ? 'Local' : 'No API Key'}
                      </Badge>
                    </div>
                    <CardDescription className="text-xs">
                      {service.description}
                    </CardDescription>
                  </CardHeader>
                  <CardContent>
                    <div className="text-xs text-gray-400 space-y-1">
                      <p>Type: {service.type}</p>
                      <p>Provider: {service.provider}</p>
                      {service.model && <p>Model: {service.model}</p>}
                    </div>
                  </CardContent>
                </Card>
              )
            })}
          </div>
        </TabsContent>

        <TabsContent value="user-management" className="space-y-6">
          <Card className="bg-black/50 border-purple-500/20">
            <CardHeader>
              <CardTitle className="text-white">User Management</CardTitle>
              <CardDescription>Manage users, roles, and permissions</CardDescription>
            </CardHeader>
            <CardContent>
              <p className="text-gray-400">User management interface coming soon...</p>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="system-logs" className="space-y-6">
          <Card className="bg-black/50 border-purple-500/20">
            <CardHeader>
              <CardTitle className="text-white">System Audit Logs</CardTitle>
              <CardDescription>View system activity and security logs</CardDescription>
            </CardHeader>
            <CardContent>
              <p className="text-gray-400">Audit log viewer coming soon...</p>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="data-export" className="space-y-6">
          <Card className="bg-black/50 border-purple-500/20">
            <CardHeader>
              <CardTitle className="text-white">Data Export</CardTitle>
              <CardDescription>Export platform data for backup or migration</CardDescription>
            </CardHeader>
            <CardContent>
              <p className="text-gray-400">Data export functionality coming soon...</p>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  )
}
